import React from 'react';
import { AppBar, Toolbar, Typography, Container, Grid, Card, CardContent, CardActions, Button, Link } from '@mui/material';
import { Calculator, TrendingUp, PieChart, Landmark } from 'lucide-react';

const tools = [
  { name: '  ', icon: <TrendingUp />, link: '/cagr-calculator' },
  { name: ' ', icon: <PieChart />, link: '/compound-interest-calculator' },
  { name: ' ', icon: <Calculator />, link: '/average-price-calculator' },
  { name: '  ', icon: <Landmark />, link: '/loan-repayment-calculator' }
];

const MainPage = () => {
  return (
    <>
      <AppBar position="static" color="primary">
        <Toolbar>
          <Typography variant="h6">  by </Typography>
        </Toolbar>
        <Link 
            href="https://minicode.tistory.com" 
            target="_blank" 
            rel="noopener noreferrer"
            sx={{ 
              color: 'white', 
              textDecoration: 'none',
              paddingLeft: '16px',
              '&:hover': {
                textDecoration: 'underline',
              },
            }}
          >
            minicode.tistory.com
          </Link>
      </AppBar>
      <Container maxWidth="md" sx={{ mt: 4 }}>
        <Grid container spacing={4}>
          {tools.map((tool) => (
            <Grid item xs={12} sm={6} md={4} key={tool.name}>
              <Card>
                <CardContent>
                  {tool.icon}
                  <Typography variant="h6" component="div" sx={{ mt: 2 }}>
                    {tool.name}
                  </Typography>
                </CardContent>
                <CardActions>                  
                  <Button component={Link} to="/#/cagr-calculator" size="small" fullWidth>
                    
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </>
  );
};

export default MainPage;